@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100;200;300;400;500;600;700;800;900&display=swap');
.container{
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}
header{
    width: 66%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 1%;
    padding-bottom: 1%;
    margin: auto;   
}   
.title{
    font-family: 'Roboto Slab', serif;
    font-weight: 900;
    font-size: 33px;
    text-align: center;
    color: #070707;
    margin-top: .8%;
    margin-bottom: 3%;
}
.header-title{
    font-family: 'Roboto Slab', serif;
    font-weight: 500;
    font-size: 18px;
    color: #828282;
    cursor: pointer;
}
.header-title:hover{
    color: #070707;
}
.line{
    width: 66%;
    background-color: #828282;
    border-color: #828282;
    padding-top: .1%;
    padding-bottom: .1%;
    margin: auto;
}
.introduce{
    width: 66%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: auto;
}
.introduce-title-one{
    width: 22%;
    font-family: 'Roboto Slab', serif;
    font-weight: 800;
    font-size: 22px;
    color: #070707;
}
.introduce-title-two{ 
    width: 33%;
    font-family: 'Roboto Slab', serif;
    font-weight: 500;
    font-size: 22px;
    color: #070707;
    text-align: end;
}
.me{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-top: 3%;
    padding-bottom: 3%;
}
.me-container{
    width: 66%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: auto;
}
.me-box{
    width: 44%;
    display: flex;
    flex-direction: column;
}
.me-title{
    font-family: 'Roboto Slab', serif;
    font-weight: 600;
    font-size: 15px;
    text-align: center;
    color: #070707;
}
.me-image{
    width: 44%;
}
.attainment{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-top: 3%;
    padding-bottom: 3%;
}
.attainment-title{
    font-family: 'Roboto Slab', serif;
    font-weight: 600;
    font-size: 15px;
    text-align: center;
    color: #070707;
}
.attainment-container{
    width: 66%;
    display: flex;
    flex-direction: column;
    margin: auto;
    padding-top: 3%;
}
.attainment-row{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 2.2%;
}
.attainment-box{
    width: 22%;
    display: flex;
    flex-direction: column;
}
.attainment-name{
    font-family: 'Roboto Slab', serif;
    font-weight: 600;
    font-size: 15px;
    text-align: center;
    color: #828282;
}
.attainment-name:hover{
    color: #070707;
}
.attainment-image{
    width: 44%;
    margin: auto;
}
.work{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-top: 3%;
}
.work-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: #070707;
}
.work-box{
    width: 44%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 3%;
    padding-bottom: 3%;
    margin: auto;
}
.work-image{
    width: 11%;
}
.work-title{
    font-family: 'Roboto Slab', serif;
    font-weight: 500;
    font-size: 22px;
    color: #F7E80F;
    margin-top: .8%;
    text-decoration: none;
}
.run-box{
    width: 100%;
    background-color: #000;
    padding-top: 1%;
    padding-bottom: 1%;
}
.run-text{
    font-family: 'Roboto Slab', serif;
    font-weight: 500;
    font-size: 55px;
    color: #F7E80F;
}
.contact{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-top: 3%;
    padding-bottom: 3%;
}
.contact-box{
    width: 33%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: auto;
}
.contact-title{
    font-family: 'Roboto Slab', serif;
    font-weight: 500;
    font-size: 22px;
    color: #000;
}
.contact-container{
    width: 11%;
    margin-top: 1%;
}
.contact-image{
    width: 100%;
}